                <p>The City eminent domain of the Cathay Mortuary properties was an arduous and lengthy process, legally led by the City Attorneys office (i.e., specifically Attny. Andrew Swart) and involving innumerable public hearings attended by a wide coalition of community non-profit organizations and countless individuals that supported the CBPRC for years. According to Terry Ow-Wing, a former  member of the CBPRC, the need for extra park space won over most people, even though some local residents might not look forward to playing in a park on the site of a former morgue. "If you believe in the community, you want to look forward and not look back," she said.<sup>10</sup></p>
